@charset "UTF-8";
/*////////////////////////////////////////////////////

印刷用CSS

////////////////////////////////////////////////////*/
@media print {
  body {
    zoom: 90%;
    -webkit-print-color-adjust: exact;
    overflow: visible !important;
  }

  header {
    position: relative;
  }

  #contents {
    margin-top: 0;
  }

  #wrapper {
    min-width: 1200px;
  }

  .box-bnr,
.boxContent04,
.map-area-box {
    page-break-inside: avoid;
  }

  .boxContent04:before {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
  }

  #btnPagetop {
    display: none;
  }

  @page {
    margin: 0 10px !important;
  }
  .box-search-map {
    display: none;
  }

  .main-text01 br.sp {
    display: none;
  }

  .header-inner {
    position: relative;
    top: 0px;
    margin: 0 auto;
    width: 100%;
    z-index: 100;
    background: rgba(255, 255, 255, 0);
  }

  .drop-gnav-wrap {
    display: none;
  }

  .sp-top-tit01 {
    display: none;
  }

  .slider-wrap {
    position: relative;
    top: -86px;
  }
  .slider-wrap .triangle-top.style01 {
    background-position: -3px 79px;
  }
  .slider-wrap .triangle-top.style02 {
    background-position: -3px 80px;
  }

  .top-contents-wrap {
    margin-top: -86px;
  }

  #gNav ul li.sp {
    display: none;
  }

  @-moz-document url-prefix() {
    .slider-wrap {
      position: relative;
      top: -87px;
    }
    .slider-wrap .triangle-top.style01 {
      background-position: -3px 79px;
    }
    .slider-wrap .triangle-top.style02 {
      background-position: -3px 80px;
    }

    #gNav {
      margin-right: 50px;
    }

    .top-contents-wrap {
      position: relative;
    }

    .top-btn-detail {
      outline: 1px solid #b1b1b1;
    }

    .list-btn-detail {
      outline: 1px solid #b1b1b1;
    }
  }
}